  padding-bottom: 20rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: max-content;
}
@media only screen and (max-width: 46.25em) {
  .section-review {
    padding-bottom: 5rem;
  }
}

.section-ad {
  background-color: #f25f3a;
  padding: 6.5rem 0;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.section-ad::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(bg-simplify-section-desktop.svg);
  background-repeat: no-repeat;
  background-size: 95%;
  background-position: 350% 100%;
  width: 100%;
  height: 100%;
  z-index: -1;
}
@media only screen and (max-width: 68.125em) {
  .section-ad::before {
    background-position: right top;
    background-size: cover;
  }
}
.section-ad__contain {
  width: 80%;
  height: 100%;
  min-width: 50%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media only screen and (max-width: 46.25em) {
  .section-ad__contain {
    flex-direction: column;
    row-gap: 5.5rem;
    text-align: center;
  }
}
.section-ad__header {
  flex: 0 0 50%;
}